import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';

class PostForm extends Component {
    constructor(props) {
        super(props);

        this.generateField = this.generateField.bind(this);
    }

    generateField(name, label, placeholder) {
        return (
            <div>
                <Field 
                    name={name} 
                    component="input" 
                    type="text"
                    placeholder={placeholder}
                    autocomplete="off"
                    className="post-input input-text marg-b-sm" 
                />
            </div>
        );
    }

    render() {
        const { handleSubmit } = this.props;
        return (
            <form onSubmit={handleSubmit}>
                {this.generateField(
                    'title', 
                    'Title',
                    'Enter a title'
                )}
                <div className="wrapper-flex">
                    <Field 
                        name="pay" 
                        component="input" 
                        type="text"
                        placeholder="Enter an offer amount"
                        autocomplete="off"
                        className="post-input input-text" 
                    />
                    <button
                        type="submit"
                        className="button-shaded marg-l-sm"
                    >Post</button>  
                </div>
            </form>
        );
    }
}

export default reduxForm({form: 'post'})(PostForm);