const layout = {
    postIsExpanded: false,
    sortIsExpanded: false
}

export default (state = layout, action) => {
    switch (action.type) {
        case 'INVERT_POST_ISEXPANDED':
            return state;
        case 'INVERT_SORT_ISEXPANDED':
            return {
                ...state,
                sortIsExpanded: !state.sortIsExpanded
            }
        default:
            return state;
    }
}